// Misc.
$misc: (
  z-index-base: 10000,
);

// Duration.
$duration: (
  menu: 0.35s,
  transition: 0.2s,
  banner: 2.5s
);

// Size.
$size: (
  border-radius: 4px,
  element-height: 2.75em,
  element-margin: 2em,
  inner: 65em
);

// Font.
$font: (
  family: ('Alegreya Sans', Helvetica, sans-serif),
  family-fixed:  ('Courier New', monospace),
  family-title: ('Montserrat', Helvetica, sans-serif),
  weight: 300,
  weight-bold:  600,
  letter-spacing:  0.025em,
  letter-spacing-alt:  0.25em
);

// Palette.
$palette: (
  bg:       #EFE3D3,
  bg-alt:   #fff,
  fg:       #0f0f0f,
  fg-bold:  #2d2e2d,
  fg-light: rgb(50, 53, 51),
  border:   rgb(39, 48, 41),
  border-bg:#958b8c,
  highlight:rgb(116, 116, 116),
  accent1:  #A5AA94,
  accent2:  #C4C9B2,
  accent3:  #F1CDB7,
  accent4:  #EF9D87,
  accent5:  #958b8c,
  accent6:  #958b8c,
  footer:   #ffffff
);